import * as React from 'react';
import styles from './navbar.module.css';
import logo from '../../images/logo.png';
import { MdMenu } from 'react-icons/md';

const Navbar = () => {

    const [open, setOpen] = React.useState(false);

    React.useEffect(() => {
        window.addEventListener('resize', () => { setOpen(false); })
    }, [])

    return (
        <div className={styles.container}>
            <div className={styles.logoContainer}>
                <img className={styles.logo} src={logo} alt="logo" />
            </div>

            <div className={styles.menuButton} onClick={() => setOpen(true)}>
                <MdMenu size={'2.5rem'} color='#33DCA3' />
            </div>
            {open ? <div className={styles.menuOverlay} onClick={() => setOpen(false)} /> : null}
            <div className={`${styles.linksContainer}${open ? ' + ' + styles.active : ''}`}>

                <a href="#"> Home </a>
                <a href="#"> Major Classes </a>
                <a href="#"> Math Classes </a>
                <a href="#"> Major Electives </a>
                <a href="#"> Non-Major Electives </a>
            </div>
        </div>
    );
}

export default Navbar;